@tailwind base;
@tailwind components;
@tailwind utilities;
@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@400;500;600;700&display=swap');
body {
    font-family: 'Nunito', sans-serif;
    @apply dark:bg-neutral-900 dark:text-gray-200 
}
.project{
    opacity: 0;
}
.showproject:hover .project{
    opacity: 1;
}
.splide__arrows{
    display: none;
}
.test>.splide__pagination{
    display: none;
}
.splide__pagination__page{
   @apply bg-gray-300
}